import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { CustomersListComponent } from './customers-list/customers-list.component';
import { FilterTextboxComponent } from './customers-list/filter-textbox.component';
import { CustomersComponent } from './customers.component';
import { LoginComponent } from './login/login.component';
import { ConsumptionComponent } from './consumption/consumption.component';
import { BillComponent } from './bill/bill.component';
import { PropertiesComponent } from './properties/properties.component';
import { GenerationComponent } from './generation/generation.component';

const routes: Routes = [
    { path: 'customers', component: CustomersComponent },
    { path: 'customer/login', component: LoginComponent },
    { path: 'customer/consumption', component: ConsumptionComponent },
    { path: 'customer/bill', component: BillComponent },
    { path: 'customer/properties', component: PropertiesComponent },
    { path: 'customer/generation', component: GenerationComponent }
];

@NgModule({
    imports: [ RouterModule.forChild(routes) ],
    exports: [ RouterModule ]
})
export class CustomersRoutingModule {}
